@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
.app {
  font-family: "Indie Flower", cursive;
}

.navbar {
  height: 120px;
  display: flex;
  align-items: flex-end;
  padding: 1.1rem;
  box-shadow: 0 0 7px gray;
  background-color: blueviolet;
  color: white;
}

.navbar-links {
  display: flex;
  align-items: flex-end;
}

.navbar-links * {
  text-decoration: none;
  color: black;
  margin-right: auto;
}

.navbar-links h1 {
  font-size: 3rem;
  font-style: cursive;
  color: white;
}

.navbar-links_logo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.navbar-links_logo img {
  height: 100px;
  width: 100px;
  border-radius: 50%;
}

.navbar-links_container {
  display: flex;
  padding: 10px;
  margin-left: 40px;
}

.navbar-links_container a {
  margin: 0 16px;
}

.navbar-links_container input {
  border-radius: 5px;
  padding: 2px 15px;
  outline: none;
  border: none;
  color: black;
}

.navbar-links_container p {
  font-size: 20px;
  color: white;
  background-color: black;
  padding: 5px 10px;
  border-radius: 5px;
}

.navbar button {
  background-color: white;
  margin: 6px 16px;
  border-radius: 7px;
  color: black;
  cursor: pointer;
  outline: none;
  font-weight: 600;  
  font-family: monospace;
}

.nfts {
  padding: 25px;
}

.nfts-container_text {
  padding-left: 50px;
}

.nfts-container_text h1 {
  font-size: 40px;
}

.nfts-container_cards {
  display: flex;
  flex-wrap: wrap;
  margin: 18px;
  min-height: 82vh;
}

.nft-card {
  height: 430px;
  width: 430px;
  margin: 16px;
  display: flex;
  flex-direction: column;      
  border: solid 5px blueviolet;
  flex-wrap: wrap;
}

.nft-card img {
  height: 100%;
  width: 100%;  
}

.nft-info {
  margin: 40px;
  min-height: 70vh;
}

.nft-section {
  display: flex;
  height: 500px;
}

.nft-img {
  width: 50%;
  text-align: right;
  padding: 20px;
}

.nft-img img {
  height: 100%;
  width: 100%;
}

.nft-details {
  padding: 20px;
}

.nft-d-owner {
  margin: 20px 0 40px 0;

}

.nft-d-owner span {
  background-color: blueviolet;
  border-radius: 5px;
  padding: 2px 5px;
  color: white;
  font-family: monospace;

}

.nft-d-name {
  text-align: center;
  font-size: 25px;
  font-weight: 700;
  text-transform: uppercase;
}

.nft-d-description {
  text-align: center;
  font-weight: 600;
  margin: 30px 0;
}

.reactions {  
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.r-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px;  
}

.r-count {
  font-size: 30px;
  font-weight: 600;
}

.r-div img {
  width: 40px;
  height: 40px;
  cursor: pointer;  
}

.comments-section {
  width: 50%;
  margin-left: auto;
  padding: 30px;
  padding-top: 0;
}


.add-comment-section {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.add-comment-section textarea {
  width: 500px;
  height: 90px;
  border-radius: 10px;
  padding: 10px;
  outline: none;
  font-family: sans-serif;
}

.add-comment-section button {
  padding: 5px 15px;
  border-radius: 5px;
  background-color: black;
  color: white;
}

.comm {
  border: solid 1px blueviolet;
  border-radius: 5px;
  margin-bottom: 10px;
}

.comm-sender {
  background-color: rgb(104, 103, 117);
  padding: 5px 10px;
  color: white;
}

.comm-sender span {
  font-family: monospace ;
  border: 1px solid white;
  padding: 2px 4px;
  border-radius: 2px;
  margin-left: 10px;
}

.comm-content {
  padding: 10px;
}

.add-c-text {
  margin-left: 30px;
  font-size: 24px;
}
.create {
  margin: 2rem;  
}

.create_head {
  font-size: 2rem;
  font-weight: 600;
}

.create_form {
  display: flex;
  flex-direction: column;  
}

.field_label {
  font-size: 20px;  
  margin: 10px 0;
}

.create_form input {
  width: 700px;
  padding: 10px 20px;
  font-family: monospace;
  outline: none;
  border: 1px solid blueviolet;
  border-radius: 5px;
}

.create_form textarea {
  width: 700px;
  height: 100px;
  padding: 10px 20px;
  font-family: monospace;
  outline: none;
  border: 1px solid blueviolet;
  border-radius: 5px;
}

.create_form button {
  width: 90px;
  height: 40px;
  margin-top: 40px;
  border-radius: 10px;
}
